
import { Menu, MenuItem, Typography } from "@material-ui/core";
import { Alert} from "@material-ui/core";
import { Grid, IconButton, div } from "@material-ui/core";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import humidity from "../images/humidity.svg";
import { useState } from 'react';
import { Confirm } from "notiflix";



const Item = ({ data, key, degUnit }) => {
    const [openOptions, setOpenOptions] = useState(null);
    const onConfirmDeleteLocation = () => {
        Confirm.show(
            "❌ Confirm Deletion",
            "Are you Sure you want to delete this location?",
            "Yes",
            "Cancel",
            () => {},
            () => {},
            {
                titleColor: '#e66146',
                fontFamily: "quicksand",
                useGoogleFont: false
            })
    }
                           
    return (
        <div class='h-full bg-black text-white flex rounded-lg p-3 border-2 flex flex-col' style={{ minWidth: '20rem'}}>
            <div id='header' class='flex flex-grow justify-between'>
                <div id='location-date' class='w-6/12'>
                    <Typography variant='p' className='font-medium' sx={{ color: 'error.main' }}>
                        {data.date}
                    </Typography>
                    <Typography variant='p' className='block  font-medium ' style={{ fontSize: '130%' }}>
                        {data.city}
                    </Typography>
                </div>

                <div id='temp' class='w-5/12 text-center self-center'>
                    <Typography variant='p' className='block text-4xl font-medium' sx={{ color: 'warning.light' }}>
                        {degUnit === 'C' ? data.degC : data.degF}°{degUnit}</Typography>
                </div>

                <div id='delete-button' className='w-1/12'>
                    <IconButton id="long-button" onClick={(e)=>setOpenOptions(e.currentTarget)}>
                        <MoreVertIcon className='text-white hover:text-green-600' />
                    </IconButton>
                    <Menu anchorEl={openOptions} open={Boolean(openOptions)} onClose={()=>setOpenOptions(null)}>
                        <MenuItem >Select Location</MenuItem>
                        <MenuItem >Update Location</MenuItem>
                        <MenuItem onClick={onConfirmDeleteLocation}>
                        Delete location
                            </MenuItem>
                    </Menu>
                </div>
            </div>

            <div id='content' class='flex'>
                <div id='weather-status' class='w-5/12 bg-red-90 flex flex-col items-center mt-3'>
                    <img width='80' src={data.icon} alt='weather' />
                    <Typography variant='p' className='text-2xl' sx={{ marginTop: '-15px' }}>{data.weatherStatus}</Typography>
                </div>

                <div className='w-7/12 flex flex-col space-y-2 mt-1'>
                    <Typography variant='p' className=' mb-5 text-center '>
                        Feels like {degUnit === 'C' ? data.degC : data.degF}°{degUnit}. {data.shortDescription}</Typography>
                    <Typography><img width='25' className='inline-block' alt='' src={humidity} />Humidity : <span style={{ color: 'yellow' }}>{data.humidity}</span></Typography>
                    <Typography> <i class="fas fa-wind" /> WindSpeed : <span style={{ color: 'yellow' }}>{data.windSpeed}</span></Typography>
                    <Typography><i class="fas fa-smog" /> Visibility  : <span style={{ color: 'yellow' }}>{data.visibility}</span></Typography>
                </div>


            </div>
        </div>

    )
}
function Dashboard({ degUnit, data }) {

    return (
        <div id='dashboard' class='p-4 bg-red-90 flex flex-col'>
            <Alert severity='info' className='mx-auto flex justify-center'>
                    <Typography class='text-blue-900 font-medium'>
                    Weather cards are slidable. Click on a  dashboard card to View its Forecast!
                </Typography>
                </Alert>
            
                <Typography  class='self-start mt-3 block  text-2xl border-2  border-yellow-200
                font-medium  bg-black p-2 rounded-lg text-blue-400' >Dashboard</Typography>
               
            
            <Grid class='flex justify-start overflow-x-auto space-x-3 mt-4'>
                {data.map((data, key) => <Item data={data} degUnit={degUnit} key={key}/>)}
            </Grid>
        </div>
    )
};


export default Dashboard;