import React, { useState } from 'react';
import NavBar from "./components/NavBar";
import cover from "./images/wallpapers/wallpaper.jpg";
import Dashboard from "./components/Dashboard";
import Forecast from "./components/Forecast";
import Options from "./components/Options";
import { Container, div } from '@material-ui/core';
import sunny from "./images/weatherStates/sunny.svg";


const useDeg = () => {
  const [degUnit, setDegUnit] = useState('F');
  const handleDegF = () => setDegUnit('F');
  const handleDegC = () => setDegUnit('C');
  return [degUnit, handleDegC, handleDegF];
}

const useOptions = () => {
  const [open, setOpen] = useState(false);
  const [wallpaper, setWallpaper] = useState(cover);
  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);
  return [open, handleOpen, handleClose, wallpaper, setWallpaper];
}


const data = [
  {
    day: 'Monday',
    city: '8417 Spruce Hill Dr',
    date: 'July, 6th, 08:37am',
    degC: '41',
    degF: '30',
    icon: sunny,
    weatherStatus: 'Sunny',
    shortDescription: 'Light rain, gentle breeze.',
    humidity: '100',
    windSpeed: '200',
    visibility: '300'
  }
  ,
  {
    day: 'Monday',
    city: 'Laurel, US',
    date: 'July, 6th, 08:37am',
    degC: '41',
    degF: '30',
    icon: sunny,
    weatherStatus: 'Sunny',
    shortDescription: 'Light rain, gentle breeze.',
    humidity: '100',
    windSpeed: '200',
    visibility: '300'
  }
];

function App() {

  const [degUnit, handleDegC, handleDegF] = useDeg();
  const [open, handleOpen, handleClose, wallpaper, setWallpaper] = useOptions();

  return (
    <div class='h-screen break-words flex block flex-col' style={{backgroundImage: `url(${wallpaper})`, backgroundSize: 'cover' }}>
       <NavBar handleOpen={handleOpen} />
      <div class=" overflow-auto flex-grow">
        <Dashboard degUnit={degUnit} data={data} />
        <Forecast degUnit={degUnit} data={data} />
        {/* <Options open={open} wallpaper={wallpaper} setWallPaper={setWallpaper} handleClose={handleClose} handleDegC={handleDegC} handleDegF={handleDegF} hidden/> */}
        </div>
      </div>
  );
}

export default App;
